.mob-menu__container {
  height: 100%;
  width: 100%;
  z-index: 99999;
  box-sizing: border-box;
  background: #fff;
  flex-direction: column;
  padding-bottom: 18px;
  transition-property: transform;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateX(100%);
}

.mob-menu__container.is-open {
  transform: translateX(0);
}

.mob-menu__container .mob-menu_header {
  width: 100%;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 24px 16px;
  display: flex;
}

.mob-menu__container .menu-close-btn {
  width: 48px;
  height: 48px;
  background: none;
  border: none;
  align-items: center;
  display: flex;
}

.mob-menu__container .menu-close {
  width: 24px;
  height: 24px;
}

.mob-menu__container .mob-navbar {
  box-sizing: border-box;
  flex: auto;
  padding-top: 68px;
}

.mob-menu__container .mob-navbar svg {
  height: 32px;
  width: 32px;
}

.mob-menu__container .mob-navbar .nav__list {
  flex-direction: column;
  display: flex;
}

.mob-menu__container .mob-navbar .nav__item {
  height: 44px;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.mob-menu__container .mob-navbar .nav__item svg:first-child {
  border: 1px solid #fff;
  border-radius: 50%;
}

.mob-menu__container .mob-navbar .nav__item svg:last-child {
  margin-top: 10px;
  margin-left: auto;
}

.mob-menu__container .mob-navbar .nav__link {
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  gap: 14px;
  padding: 6px 16px;
  font-weight: 400;
  display: flex;
}

.mob-menu__container .mob-navbar .nav__link--current {
  color: #fff;
  background: #4440f6;
}

.mob-menu__container .mob-navbar .nav__link--current .arrow {
  fill: #fff;
}

.mob-menu__container .theme-switch-mob {
  padding: 0 16px;
}

